.label {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.text {
  font-weight: 600;
  color: var(--main-text-color);
  margin-bottom: 10px;
}

.input {
  width: 70%;
  height: 40px;
  padding: 0 10px 0 10px;
  border: 1px solid var(--second-bg-color);
  outline: none;
  color: var(--main-text-color);
  font-size: 14px;
}

.input:focus {
  border-color: var(--accent-bg-color);
}

.input::placeholder {
  font-size: 12px;
  color: red;
}
